.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  --minutes-arrow-speed: 3600s;
  --seconds-arrow-speed: 60s;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up {
  --minutes-arrow-speed: 600s;
  --seconds-arrow-speed: 10s;
}

.stopwatch__minutes {
  animation: arrow-rotate var(--minutes-arrow-speed) steps(60) infinite;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

.stopwatch__seconds {
  animation: arrow-rotate var(--seconds-arrow-speed) linear infinite;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

@keyframes arrow-rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
